.motel-page {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  text-align: center;
}

.motel-list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 16px;
  margin-top: 20px;
}

.motel-item {
  background: white;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 10px;
  text-align: center;
  transition: transform 0.2s ease-in-out;
}

.motel-item:hover {
  transform: scale(1.05);
}

.motel-item img {
  width: 100%;
  height: 150px;
  object-fit: cover;
  border-radius: 8px;
}

h2 {
  font-size: 16px;
  margin: 10px 0;
  color: #007bff;
}

p {
  font-size: 14px;
  color: #555;
}

/* 페이지네이션 */
.pagination {
  display: flex;
  justify-content: center;
  margin-top: 20px;
  list-style: none;
  padding: 0;
  border: none;
}

.pagination li {
  display: inline-block;
  margin: 0 5px;
  border: none;
}

.pagination a {
  padding: 8px 12px;
  border: none;
  color: #007bff;
  border-radius: 4px;
  cursor: pointer;
  text-decoration: none; 
}

.pagination .active a {
  background-color: #007bff;
  color: white;
  font-weight: bold;
  border: none; 
}